import React, { createContext, useEffect, useState } from 'react'
import './App.css'
import { useLocation } from 'react-router-dom'

import Header from './components/Header'
import Body from './components/Body'
import Guide from './components/Guide'
import Content from './components/Content'
import Setting from './components/Setting'
import Axios from 'axios'
import Meta from './components/Meta'

const AppContext = createContext()

function App() {
	const location = useLocation() //ÌéòÏù¥ÏßÄ Í≤ΩÎ°ú Î≥ÄÍ≤Ω Í∞êÏßÄ
	const [ready, setReady] = useState(false)
	const [login, setLogin] = useState(false)
	const [menus, setMenus] = useState([
		{ name: 'home', count: 1, order: 1 },
		{ name: 'programming', order: 2 },
		{ name: 'article', order: 3 },
	])

	const resizeTextarea = (e) => {
		//ÌÖçÏä§Ìä∏ÏóêÏñ¥Î¶¨Ïñ¥Î•º Ï∞æÏïÑÏÑú ÌÅ¨Í∏∞Î•º Í∏ÄÏûê ÎÜíÏù¥Ïóê ÎßûÍ≤å Î≥ÄÍ≤ΩÌïòÍ∏∞
		//textarea ÎÜíÏù¥ Ï°∞Ï†ï
		const fake = document.querySelector('#fakeTextarea')
		let textareas = []
		if (e && e.target) {
			textareas.push(e.target)
		} else {
			textareas = document.querySelectorAll('textarea')
		}

		if (textareas && fake) {
			for (let i = 0; i < textareas.length; i++) {
				fake.style.height = '1px'
				fake.style.width = textareas[i].clientWidth + 'px'
				fake.value = textareas[i].value
				textareas[i].style.height = 5 + fake.scrollHeight + 'px'
			}
			fake.value = ''
			fake.style.height = '0px'
		}
	}

	const slideMenu = () => {
		//Î©îÎâ¥Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú Î©îÎâ¥ Î≥¥Ïù¥Í∏∞ & Ïà®Í∏∞Í∏∞
		const body = document.querySelector('#body')
		const guideWrapper = document.querySelector('#guideWrapper')
		const content = document.querySelector('#content')

		if (!guideWrapper.style.left) {
			//Ïä§ÌÉÄÏùº ÏÜçÏÑ±Ïù¥ ÏóÜÏúºÎ©¥ Ï¥àÍ∏∞Í∞í ÏßÄÏ†ï
			if (body.clientWidth < 500) {
				guideWrapper.style.left = '-312px' //Î™®Î∞îÏùº
			} else {
				guideWrapper.style.left = '0px' //PC
			}
		}

		if (guideWrapper.style.left && guideWrapper.style.left.replace('px', '') > -100) {
			//Î©îÎâ¥ Îã´Í∏∞
			guideWrapper.parentNode.style.width = '0' // ÌöåÏÉâ 0
			content.style.width = 'calc(100% - 16px)' //ÏΩòÌÖêÌä∏ 100% - 16px
			guideWrapper.style.left = '-312px' // Î©îÎâ¥ ÏôºÏ™ΩÏúºÎ°ú
		} else {
			//Î©îÎâ¥ Ïó¥Í∏∞
			guideWrapper.style.left = '0px' // Î©îÎâ¥ 0
			if (body.clientWidth < 500) {
				//Î™®Î∞îÏùº
				guideWrapper.parentNode.style.width = '100%' // ÌöåÏÉâ 100%
			} else {
				//PC
				content.style.width = `calc(100% - 312px - 16px)`
			}
		}
	}
	//Î™®Î∞îÏùºÏóêÏÑú ÌöåÏÉâÎ∂ÄÎ∂Ñ ÌÅ¥Î¶≠ Ïãú Î©îÎâ¥ Îã´Í∏∞
	const closeMenuMobile = (e) => {
		const body = document.querySelector('#body')
		const guideWrapper = document.querySelector('#guideWrapper')
		const content = document.querySelector('#content')

		guideWrapper.parentNode.style.width = '0' // ÌöåÏÉâ 0
		if (body.clientWidth < 500) {
			//Î™®Î∞îÏùº
			guideWrapper.style.left = '-312px' // Î©îÎâ¥ 0
			content.style.width = 'calc(100% - 16px)'
		} else {
			content.style.width = 'calc(100% - 312px - 16px)'
		}
	}
	const checkToken = (func) => {
		//Î°úÍ∑∏Ïù∏ ÎêòÏñ¥ÏûàÎäîÏßÄ ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ÌïòÍ∏∞
		let url = process.env.REACT_APP_URL + '/auth/check'
		//url = process.env.REACT_APP_LOCAL_URL+'/auth/check'
		Axios.get(url, { withCredentials: true })
			.then((res) => {
				if (res.data) {
					//console.log('ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ ÏÑ±Í≥µ')
					setLogin(res.data)
				} else {
					//console.log('ÌÜ†ÌÅ∞ ÏóÜÏùå') //res.status===204(No Content)
					setLogin(false)
				}
				if (func) func()
			})
			.catch((e) => {
				//Ïã§Ìå®
				if (func) func()
			})
	}

	//useContext Ïù¥Ïö©ÌïòÏó¨ ÌïòÏúÑ Ïª¥Ìè¨ÎÑåÌä∏Ïóê ÏÉÅÌÉú Ï†ÑÎã¨
	const store = {
		ready,
		setReady,
		login,
		setLogin,
		menus,
		setMenus,
		resizeTextarea,
		slideMenu,
		closeMenuMobile,
		checkToken,
	}

	// Ìè¨Ïä§Ìä∏ÏóêÏÑú ÌÉúÍ∑∏ Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏ÏôÄÏÑú Î©îÎâ¥Ïóê ÌëúÏãúÌï®, QuillÏùò ÌÉúÍ∑∏Î™©Î°ùÏóê ÌëúÏãú
	useEffect(() => {
		let url = process.env.REACT_APP_URL + '/menus'
		//url = process.env.REACT_APP_LOCAL_URL+'/menus'

		Axios.get(url, {
			withCredentials: true,
		}) //Î©îÎâ¥ ÌÉúÍ∑∏ Í∞ÄÏ†∏Ïò§Í∏∞
			.then((res) => {
				setMenus(res.data)
			})
			.catch((e) => console.log(e)) //Ïã§Ìå®

		//ÌéòÏù¥ÏßÄ Ïù¥ÎèôÌï† Îïå activeListManual Ïä§ÌÉÄÏùºÏùÑ Î™®Îëê ÏßÄÏõÄ
		const tags = document.querySelectorAll('.activeListManual')
		if(tags){
			for(let i of tags){
				i.classList.remove('activeListManual')
			}
		}
	}, [location.pathname])

	//Ï£ºÏÜå Î≥ÄÍ≤ΩÎê† Îïå, ÌÜ†ÌÅ∞ Ï≤¥ÌÅ¨ÌïòÍ≥† Ìè¨Ïä§Ìä∏ Ï°∞ÌöåÌïòÍ∏∞
	useEffect(() => {
		checkToken()
	}, [location.pathname, location.search])

	return (
		<AppContext.Provider value={store}>
			<div id="app">
				<Meta
					data={{
						title: 'ÏûÑÍ∏∞Ïùò ÏΩîÎî© Î∏îÎ°úÍ∑∏ üòÑ',
						description: 'imki123Ïùò ÏûÑÍ∏∞Ïùò ÏΩîÎî© Î∏îÎ°úÍ∑∏ÏûÖÎãàÎã§ üòÑ',
						image: process.env.PUBLIC_URL + '/images/imcat_800x400.png',
					}}
				/>
				<Header />
				<Setting />
				<Body>
					<Guide />
					<Content />
				</Body>
			</div>
		</AppContext.Provider>
	)
}
export { AppContext }
export default React.memo(App)
