/* eslint-disable jsx-a11y/accessible-emoji */
import React, { useEffect, useState } from 'react'
import './Quill.css'
import 'quill/dist/quill.snow.css'
import { useQuill } from 'react-quilljs'
import imageCompress from 'quill-image-compress'
import { AppContext } from '../App'
import Axios from 'axios'
import { CopyToClipboard } from 'react-copy-to-clipboard';


function Quill({ match, location, history }) {
	const store = React.useContext(AppContext)
	let { postId } = match.params
	const [post, setPost] = useState()
	const [ready, setReady] = useState()
	const [subMenus, setSubMenus] = useState()

	const modules = {
		toolbar: [
			['bold', 'italic', 'underline', 'strike'],
			[{ size: ['small', false, 'large', 'huge'] }, { header: 1 }, { header: 2 }],
			[{ align: [] }],
			[{ color: [] }, { background: [] }],
			[{ indent: '-1' }, { indent: '+1' }],
			[{ list: 'ordered' }, { list: 'bullet' }],
			['code-block', 'blockquote'],
			['link', 'image', 'video'],
			['clean'],
		],
		syntax: true,
		imageCompress: {
			quality: 0.7, // default
			maxWidth: 1000, // default 1000
			maxHeight: 1000, // default 1000
			imageType: 'image/jpeg', // default
			debug: true, // default
		  }
	}
	const formats = ['bold', 'italic', 'underline', 'strike', 'code-block', 'blockquote', 'size', 'header', 'align', 'color', 'background', 'indent', 'list', 'link', 'image', 'video', 'clean']

	const { quill, quillRef, Quill } = useQuill({ modules, formats })
	if (Quill && !quill) {
		// For execute this line only once.
		Quill.register('modules/imageCompress', imageCompress)
	}

	useEffect(() => {
		setReady(false)
		//Ìè¨Ïä§Ìä∏ Î∂àÎü¨Ïò§Í∏∞ axios
		if (postId !== undefined && Number(postId) >= 1 && quill) {
			//postIdÍ∞Ä ÏóÜÏúºÎ©¥ Ìè¨Ïä§Ìä∏ ÎÇ¥Ïö© Í∞ÄÏ†∏Ïò§ÏßÄ ÏïäÍ∏∞
			let url = process.env.REACT_APP_URL + '/posts/' + postId
			//url = process.env.REACT_APP_LOCAL_URL+'/posts/' + postId

			Axios.get(url, {
				withCredentials: true,
			}) //Ìè¨Ïä§Ìä∏ Î∂àÎü¨Ïò§Í∏∞
				.then((res) => {
					let title = document.querySelector('[name=title]')
					title.value = res.data.title

					const tags = document.querySelectorAll('[type=radio]')
					for (let i of tags) {
						//Ï≤¥ÌÅ¨ Ï¥àÍ∏∞Ìôî
						i.checked = false
					}
					if (res.data.tags) {
						//Ï≤¥ÌÅ¨Î∞ïÏä§ Ï≤¥ÌÅ¨
						const mainMenu = document.getElementById(res.data.tags[0])
						if (mainMenu) mainMenu.checked = true
						for (let i of store.menus) {
							if (i.name === res.data.tags[0]) {
								//ÏÑ†ÌÉùÎêú Î©îÏù∏Î©îÎâ¥Ïùò ÏÑúÎ∏åÎ©îÎâ¥Î•º Í∑∏Î¶¨Í∏∞
								setSubMenus(i.subMenus)
							}
						}
					}

					//Ìè¨Ïä§Ìä∏Î∞îÎîî Í∞ÄÏ†∏Ïò§Í∏∞
					url = process.env.REACT_APP_URL + '/posts/postBody/' + postId
					//url = process.env.REACT_APP_LOCAL_URL + '/posts/postBody/' + postId
					Axios.get(url)
						.then((res2) => {
							if (typeof res2.data.body === 'string') quill.setText(res2.data.body)
							//bodyÍ∞Ä stringÏù¥Î©¥ setText
							else quill.setContents(res2.data.body) //bodyÍ∞Ä stringÏù¥ ÏïÑÎãàÎ©¥ setContents : Delta
							setPost({
								...res.data,
								body: res2.data.body,
							})
						})
						.catch((e) => {
							alert(e)
							setReady(true)
						}) //Ïã§Ìå®
				})
				.catch((e) => {
					alert(e)
					setReady(true)
				}) //Ïã§Ìå®
		}else{
			setReady(true)
		}
	}, [location, quill, postId, store.menus])

	useEffect(() => {
		if (subMenus && post) {
			const subMenu = document.getElementById(post.tags[1])
			if (subMenu) subMenu.checked = true
		}
		const newSubMenu = document.querySelector('[name=newSubMenu]')
		if (newSubMenu) newSubMenu.value = ''
	}, [subMenus, post])

	useEffect(() => {
		store.setReady(false)
		if(post || ready){
			store.setReady(true)
		}
	})

	//Í∏Ä ÏûëÏÑ± or ÏàòÏ†ï
	const clickPost = (e) => {
		if (!store.login || (store.login && store.login.username !== 'imki123')) {
			alert('Í∏Ä ÏûëÏÑ±ÏùÄ Î∏îÎ°úÍ∑∏ Ï£ºÏù∏Îßå Í∞ÄÎä•Ìï©ÎãàÎã§ ^^;')
			return
		}
		if (!window.confirm('Í∏ÄÏùÑ Í≤åÏãúÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) {
			return
		}
		//Ï†úÎ™©, ÎÇ¥Ïö©, ÌÉúÍ∑∏Í∞Ä ÏûàÎäîÏßÄ Í≤ÄÏÇ¨
		let title = document.querySelector('[name=title]')
		const content = quill.getContents()
		const text = quill.getText()
		const mainMenu = document.querySelector('[name=mainMenu]:checked')
		let tags = []
		const subMenu = document.querySelector('[name=subMenu]:checked')
		const newMainMenu = document.querySelector('[name=newMainMenu]')
		const newSubMenu = document.querySelector('[name=newSubMenu]')

		if (mainMenu) {
			tags[0] = mainMenu.value
		}
		if (newMainMenu.value !== '') {
			tags[0] = newMainMenu.value
		}
		if (subMenu) {
			if (subMenu.value !== '') tags[1] = subMenu.value //Ï≤¥ÌÅ¨ Îêú ÏÑúÎ∏åÎ©îÎâ¥ Ï∂îÍ∞Ä
		}
		if (newSubMenu.value !== '') {
			tags[1] = newSubMenu.value
		}

		if (title.value.length === 0) {
			alert('Ï†úÎ™©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
			return
		} else if (quill.getLength() === 1) {
			alert('ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.')
			return
		} else if (tags.length < 1 && newMainMenu.value === '') {
			alert('Î©îÏù∏Î©îÎâ¥Î•º 1Í∞ú ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.')
			return
		}

		//urlÏóê POST ÎòêÎäî PATCH ÏöîÏ≤≠
		let url = process.env.REACT_APP_URL + '/posts'
		//url = process.env.REACT_APP_LOCAL_URL + '/posts'
		let method = 'POST',
			message = 'Í∏Ä ÏûëÏÑ± ÏÑ±Í≥µ'
		if (e.target.id === 'PATCH') {
			if (postId !== undefined && Number(postId) >= 1) {
				url += '/' + postId
				method = 'PATCH'
				message = 'Í∏Ä ÏàòÏ†ï ÏÑ±Í≥µ'
			} else {
				console.log('postId ÎπÑÏ†ïÏÉÅ, Í∏Ä ÏàòÏ†ï Ïã§Ìå®')
				return
			}
		}

		Axios(url, {
			//Ìè¨Ïä§Ìä∏ ÏûëÏÑ±, ÏàòÏ†ï (ÏµúÎåÄ 10mb. koa-bodyparserÏóêÏÑú ÏÑ§Ï†ï.)
			method: method,
			withCredentials: true, //CORS
			data: {
				title: title.value,
				body: content,
				text: text,
				tags: tags,
			},
		})
			.then((res) => {
				alert(message) //ÏÑ±Í≥µ
				postId = res.data.postId
				if (postId > 1) {
					history.push(`/posts/${postId}`) //ÏàòÏ†ï ÏÑ±Í≥µÌïòÎ©¥ Ìï¥Îãπ Í∏ÄÎ°ú Ïù¥ÎèôÌï®
				} else {
					history.push(`/posts/1`) //Ïù∏ÏÇ¨ÎßêÏùÄ ÌôàÏúºÎ°ú Ïù¥Îèô
				}
			})
			.catch((e) => alert(e)) //Ïã§Ìå®
	}

	// ÌôîÎ©¥ Î¶¨ÏÇ¨Ïù¥Ï¶àÏãú editor ÏïÑÎûò ÎßàÏßÑ Î≥ÄÍ≤Ω
	useEffect(() => {
		const editor = document.querySelector('#editor')
		const toolbar = document.querySelector('.ql-toolbar')
		if (editor && toolbar) {
			editor.style.marginBottom = toolbar.clientHeight + 10 + 'px'
		}
		window.removeEventListener('resize', function () {})
		window.addEventListener('resize', function () {
			if (editor && toolbar) {
				editor.style.marginBottom = toolbar.clientHeight + 10 + 'px'
			}
		})
	}, [location])

	//Î©îÏù∏Î©îÎâ¥ Ï∂îÍ∞ÄÏãú ÎùºÎîîÏò§Î∞ïÏä§ Ï≤¥ÌÅ¨ Ìï¥Ï†ú, ÎùÑÏñ¥Ïì∞Í∏∞Î•º _Î°ú Î≥ÄÍ≤Ω
	const changeMainMenu = (e) => {
		if (store.menus) {
			for (let i of store.menus) {
				if (i.name === e.target.value) {
					setSubMenus(i.subMenus)
				}
			}
		}

		if (e.target.type !== 'radio') {
			//Ïù∏ÌíãÎ∞ïÏä§ ÏûÖÎ†•ÌïòÎ©¥ ÎùºÎîîÏò§ Ï≤¥ÌÅ¨ Ìï¥Ï†ú
			e.target.value = e.target.value.replace(/\s/g, '_')
			const mainMenu = document.querySelector('[name=mainMenu]:checked')
			if (mainMenu) mainMenu.checked = false
		}
	}
	//ÏÑúÎ∏åÎ©îÎâ¥ Ï∂îÍ∞ÄÏãú ÎùºÎîîÏò§Î∞ïÏä§ Ï≤¥ÌÅ¨ Ìï¥Ï†ú, ÎùÑÏñ¥Ïì∞Í∏∞Î•º _Î°ú Î≥ÄÍ≤Ω
	const changeSubMenu = (e) => {
		if (e.target.type !== 'radio') {
			//Ïù∏ÌíãÎ∞ïÏä§ ÏûÖÎ†•ÌïòÎ©¥ ÎùºÎîîÏò§ Ï≤¥ÌÅ¨ Ìï¥Ï†ú
			e.target.value = e.target.value.replace(/\s/g, '_')
			const subMenu = document.querySelector('[name=subMenu]:checked')
			if (subMenu) subMenu.checked = false
		} else {
			const newSubMenu = document.querySelector('[name=newSubMenu]')
			if (newSubMenu) newSubMenu.value = ''
		}
	}
	const copied = e => {
		//console.log(e.target)
		let emoji = e.target
		emoji.style.background = 'blue'
		setTimeout(function(){
			emoji.style.background = 'unset'
		}, 1000)
	}

	return (
		<div className="quill">
			<div className="quillTitle">
				<input name="title" placeholder="Ï†úÎ™©" />
			</div>
			<div id="editor">
				<div ref={quillRef} />
				<div id="tags">
					<div id="tagsTitle">
						tags
						{postId !== undefined && Number(postId) >= 1 ? (
							<button className="editorButton no-drag" onClick={clickPost} id="PATCH">
								Í∏Ä ÏàòÏ†ï
							</button>
						) : (
							<button className="editorButton no-drag" onClick={clickPost}>
								ÏÉàÍ∏Ä ÏûëÏÑ±
							</button>
						)}
						<span className="Emoji">
                <CopyToClipboard text={"üòÑ"}><span onClick={copied}>üòÑ</span></CopyToClipboard>
                <CopyToClipboard text={"ü§£"}><span onClick={copied}>ü§£</span></CopyToClipboard>
                <CopyToClipboard text={"ü•∞"}><span onClick={copied}>ü•∞</span></CopyToClipboard>
                <CopyToClipboard text={"üòç"}><span onClick={copied}>üòç</span></CopyToClipboard>
                <CopyToClipboard text={"üòù"}><span onClick={copied}>üòù</span></CopyToClipboard>
                <CopyToClipboard text={"üò≠"}><span onClick={copied}>üò≠</span></CopyToClipboard>
                <CopyToClipboard text={"üëç"}><span onClick={copied}>üëç</span></CopyToClipboard>
                <CopyToClipboard text={"üêï"}><span onClick={copied}>üêï</span></CopyToClipboard>
                <CopyToClipboard text={"üêà"}><span onClick={copied}>üêà</span></CopyToClipboard>
                <CopyToClipboard text={"‚òÄ"}><span onClick={copied}>‚òÄ</span></CopyToClipboard>
                <CopyToClipboard text={"üåô"}><span onClick={copied}>üåô</span></CopyToClipboard>
                <CopyToClipboard text={"‚≠ê"}><span onClick={copied}>‚≠ê</span></CopyToClipboard>
                <CopyToClipboard text={"‚òÅ"}><span onClick={copied}>‚òÅ</span></CopyToClipboard>
                <CopyToClipboard text={"üåß"}><span onClick={copied}>üåß</span></CopyToClipboard>
                <CopyToClipboard text={"üíß"}><span onClick={copied}>üíß</span></CopyToClipboard>
                <CopyToClipboard text={"‚ô•"}><span onClick={copied}>‚ô•</span></CopyToClipboard>
                <CopyToClipboard text={"‚ù§"}><span onClick={copied}>‚ù§</span></CopyToClipboard>
                <CopyToClipboard text={"üíï"}><span onClick={copied}>üíï</span></CopyToClipboard>
              </span>
              <span
                className="EmojiLink"
                onClick={() => {
                  window.open('https://copy.emojiall.com/ko/')
                }}
              >
                [Ïù¥Î™®ÏßÄüîó]
              </span>
					</div>
					<div>
						Î©îÏù∏Î©îÎâ¥:
						{store.menus &&
							store.menus.map((i) => (
								<label key={i.name}>
									<input type="radio" name="mainMenu" value={i.name} id={i.name} onClick={changeMainMenu} />
									{i.name}
								</label>
							))}
						<input name="newMainMenu" placeholder="Î©îÏù∏Î©îÎâ¥ Ï∂îÍ∞Ä" onChange={changeMainMenu} autoComplete="off"></input>
					</div>
					<div>
						ÏÑúÎ∏åÎ©îÎâ¥:
						<label>
							<input type="radio" name="subMenu" value="" onClick={changeSubMenu} /> ÏÑ†ÌÉùÏïàÌï®
						</label>
						{subMenus &&
							subMenus.map((i) => (
								<label key={i.name}>
									<input type="radio" name="subMenu" value={i.name} id={i.name} onClick={changeSubMenu} /> {i.name}
								</label>
							))}
						<input name="newSubMenu" placeholder="ÏÑúÎ∏åÎ©îÎâ¥ Ï∂îÍ∞Ä" autoComplete="off" onChange={changeSubMenu}></input>
					</div>
				</div>
			</div>
		</div>
	)
}
export default React.memo(Quill)
